.history {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;

  min-height: 90vh;

  &__table {
    max-width: 1700px;
    border-collapse: collapse;
    font-size: 20px;
    margin-top: 80px;
    margin-bottom: 80px;
    font-family: "Josefin Sans", sans-serif;
  }
  &__title {
    font-size: 4rem;
    margin-top: 80px;
    font-family: "Josefin Sans", sans-serif;
    &--details {
      font-size: 1rem;
      margin-top: 0;
      text-align: center;
      color: #e28741;
      font-style: italic;
    }
  }

  &__thead {
    border: 1px solid black;
    background-color: rgb(159, 207, 227, 0.2);
  }

  &__th {
    vertical-align: top;
    text-align: center;
    padding: 10px;
    border: 1px solid black;
  }
  &__td {
    text-align: center;
    padding: 8px;
    border: 1px solid black;
    padding: 20px;
  }

  &__tr {
    border: 1px solid black;
    &--bis {
      cursor: pointer;

      &:hover {
        background-color: rgb(226, 135, 65, 0.3);
      }
    }
  }

  @keyframes move {
    100% {
      transform: translate3d(0, 0, 1px) rotate(360deg);
    }
  }

  &__background {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    background: #ffffff;
    overflow: hidden;
    z-index: -1;
  }

  &__background span {
    width: 50vmin;
    height: 50vmin;
    border-radius: 50vmin;
    backface-visibility: hidden;
    position: absolute;
    animation: move;
    animation-duration: 1;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }

