@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@100&display=swap");

.cart {
  padding: 5px;
  font-family: "Josefin Sans", sans-serif;
  display: flex;
  flex-direction: column;
  min-height: 90vh;
  margin-left: 100px;
  margin-right: 100px;
  margin-top: 50px;
  margin-bottom: 50px;
  background-color: rgb(159, 207, 227, 0.2);

  padding: 50px;
  border-radius: 50px;

  &__empty {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__item {
    display: flex;
    flex-direction: row;
    margin: 50px 50px 50px 50px;
  }
  &__img {
    width: 250px;
    height: 150px;
    border-radius: 50px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  }

  &__description-box {
    margin-left: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__text {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 20px;
    &--price {
      font-family: "Raleway", sans-serif;
      font-size: 20px;
    }
  }

  &__bar-container {
    display: flex;
    justify-content: center;
  }
  &__bar {
    background-color: #ccc;
    height: 1px;
    width: 90%;
  }

  &__continue-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 50px;
  }
  &__continue {
    margin-bottom: 25px;
    padding: 15px;
    text-decoration: none;
    list-style-type: none;
    cursor: pointer;
    font-size: 20px;
    border-radius: 15px;
    background-color: rgb(226, 135, 67, 0.5);
    &:visited {
      color: black;
    }
  }
  &__button {
    border: 1px solid grey;
    background-color: black;
    margin-right: 1rem;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    &--right {
      margin-left: 1rem;
    }
    &--clear {
      background-color: rgb(226, 135, 67, 0.4);
      border-radius: 50px;
      border-width: 0;
      box-sizing: border-box;
      cursor: pointer;
      font-size: 20px;
      height: 44px;
      line-height: 1.15;
      margin: 0;
      padding: 0 25px;
      position: relative;
      text-align: center;
      font-family: "Josefin Sans", sans-serif;
      width: 15rem;

      &:hover {
        box-shadow: rgba(50, 50, 93, 0.1) 0 0 0 1px inset,
          rgba(50, 50, 93, 0.1) 0 2px 5px 0, rgba(0, 0, 0, 0.07) 0 1px 1px 0;
      }
    }

    &--checkout {
      background-color: rgb(226, 135, 67, 0.4);
      border-radius: 50px;
      border-width: 0;
      box-sizing: border-box;
      cursor: pointer;
      font-size: 20px;
      height: 44px;
      line-height: 1.15;
      padding: 0 25px;
      position: relative;
      text-align: center;
      font-family: "Josefin Sans", sans-serif;
      width: 15em;

      &:hover {
        box-shadow: rgba(50, 50, 93, 0.1) 0 0 0 1px inset,
          rgba(50, 50, 93, 0.1) 0 2px 5px 0, rgba(0, 0, 0, 0.07) 0 1px 1px 0;
      }
    }
  }

  &__summary {
    margin-right: 50px;
    margin-top: 5rem;
    display: flex;
    justify-content: space-between;
  }
  &__checkout {
    display: flex;
    flex-direction: column;
  }

  &__item-quantity {
    font-size: 20px;
  }

  &__subtotal {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  &__price {
    margin-left: 20px;
    font-family: "Raleway", sans-serif;
    font-size: 30px;
    color: rgb(226, 135, 67, 1);
  }

  &__total-quantity {
    font-size: 25px;
    margin: 20px;
    color: rgb(226, 135, 67, 0.7);
  }
}
// Width breakpoints
